import React, { Component } from "react";
import { Button, Form, Container } from "react-bootstrap";
import Service from "../../service/Auth.service";
import { Link } from "react-router-dom";
import EmailButton from "../ui/EmailButton";

class ViewProfile extends Component {
  constructor(props) {
    super(props);
    this._service = new Service();
    this.state = { user: {} };
  }

  componentDidMount() {
    this._service
      .viewProfile(this.props.match.params.id)
      .then(theUser => {
        this.setState({ user: theUser.data });
      })
      .catch(err => console.log({ err }));
  }

  render() {
    if (this.state.user && !this.state.user.age) {
      return (
        <>
          <h1>User name: {this.state.user.username}</h1>
          <h1>Email :{this.state.user.email}</h1>
          <h1>Experience as teacher :{this.state.user.experience}</h1>
          <img src={this.state.user.imgPath} alt=""></img>
          <Button variant="dark" as={Link} to="/main">
            <EmailButton value={this.state} />
            Back
          </Button>
        </>
      );
    } else {
      return (
        <>
          <h1>User name: {this.state.user.username}</h1>
          <h1>Email :{this.state.user.email}</h1>
          <h1>User age{this.state.user.age} years old</h1>
          <h1>User skill level is :{this.state.user.level}</h1>
          <img src={this.state.user.imgPath} alt=""></img>
          <EmailButton value={this.state} />
          <Button variant="dark" as={Link} to="/main">
            Back
          </Button>
        </>
      );
    }
  }
}
export default ViewProfile;
